.container {
    display: flex;
    flex-direction: column;

    width: 75%;
    height: 85vh;
    margin-left: 12.5%;

    background: var(--color-content__day);
    /*border: 1px solid lightgray;*/
    border-radius: 10px;
    overflow: hidden;
    box-shadow: var(--box-shadow);
}

.users {
    display: flex;
    justify-content: center;
    /*width: 100%;*/
    height: 50px;
    border-bottom: 1px solid lightgray;
    /*margin-left: 20%;*/
}

.users h3 {
    margin: auto auto auto 5%;
    color: var(--color-text__day);
}

.items {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 100%;
}

.items::-webkit-scrollbar {
    width: 4px;
}

.items::-webkit-scrollbar-track {
    background: var(--color-body__day);
}

.items::-webkit-scrollbar-thumb {
    background: var(--color-text__day);
    border: none;
    border-radius: 10px;
}

.items::-webkit-scrollbar-thumb:hover {
    border-radius: 15px;
}