.item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.input {
    width: 70%;
    /* margin-left: 15%; */
}

.input input {
    width: 100%;
    height: 2rem;
    margin-top: .5rem;
    padding: 2px 5px;
    border: none;
    border: 2px solid silver;
    border-radius: 5px;
    background: transparent;
    color: black;
    font-size: 1rem;
    transition: .2s;
    font-weight: bold;
}

.input input:hover {
    border-color: #343738;
}

.input input:focus {
    border-color: black;
    outline: none;
}

.input input::placeholder {
    color: silver;
}

.errorZone {
    display: flex;
    align-items: center;
    width: 25%;
    font-size: .8rem;
}

.error {
    color: red;
    font-weight: bold;
    /* font-size: .72rem; */
}

.error input {
    border-color: red;
    color: red;
}

.error input:hover {
    border-color: red;
}

.error input:focus {
    border-color: red;
}