/* ==> NIGHT <== */
.profile {
    display: flex;
    flex-direction: column;
    width: 75%;
    /*height: 85vh;*/
    margin-left: 12.5%;
    background: var(--color-content__night);
    border-radius: 15px;
    padding: 20px 10px;
}

.container {
    display: flex;
    justify-content: space-around;
    /*margin-top: 20px;*/
    /*background: white;*/
}

.info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 65%;
    font-family: 'Open Sans', sans-serif;
    color: #fff;
}

.username {
    display: flex;
    width: 100%;
    /* justify-content: space-between; */
}

.username h4 {
    margin: auto;
    margin-right: 0;
}

.avatarPhoto {
    display: flex;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    /*overflow: hidden;*/
}

.photoNull {
    width: 150px;
    height: 150px;
    background: lightgray;
    border-radius: 50%;
}

.fullName {
    font-size: 1.8rem;
}

.looking {
    display: flex;
    justify-content: space-between;
}

.description {
    width: 70%;
}


/* ==> DAY <== */
.profile__day {
    display: flex;
    flex-direction: column;
    width: 75%;
    /*height: 85vh;*/
    margin-left: 12.5%;
    background: var(--color-content__day);
    border-radius: 15px;
    padding: 20px 10px;
    box-shadow: var(--box-shadow);
}

.container__day {
    display: flex;
    justify-content: space-around;
    /*margin-top: 20px;*/
    /*background: white;*/
}

.info__day {
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    width: 65%;
    font-family: 'Open Sans', sans-serif;
    color: var(--color-text__day);
}

.userData {
    margin-top: 10px;
}

.avatarPhoto__day {
    display: flex;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    /*overflow: hidden;*/
}

.photoNull__day {
    width: 150px;
    height: 150px;
    background: var(--color-body__day);
    border-radius: 50%;
}

.name_logout {
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: #fff;
}

.fullName__day {
    color: var(--color-text__day);
    font-size: 1.8rem;
}

.logoutBtn {
    cursor: pointer;
    background: #fff;
    padding: 0 7px;
    color: red;
    font-size: .8rem;
    border: none;
    font-weight: bold;
}

.job {
    display: flex;
}

.job p {
    margin-left: 4px;
}

.looking__day {
    color: var(--color-text__day);
    display: flex;
    justify-content: space-between;
}

.description__day {
    color: var(--color-text__day);
    width: 70%;
}

.aboutMe {
    display: flex;
    color: var(--color-text__day);
}

.aboutMe p {
    margin-left: 4px;
}

.statusPlace {
    display: flex;
    width: auto;
}

.statusPlace h4 {
    padding-right: 4px;
}