.pagination {
    overflow-x: scroll;
}

.pagination::-webkit-scrollbar {
    width: 4px;
}

.pagination::-webkit-scrollbar-track {
    background: transparent;
}

.pagination::-webkit-scrollbar-thumb {
    background: var(--color-text__day);
    border: none;
    border-radius: 10px;
}

.pagination::-webkit-scrollbar-thumb:hover {
    border-radius: 15px;
}

.active {
    font-weight: bold;
    cursor: pointer;
}

.nonActive {
    cursor: pointer;
}
