.container {
    display: flex;
    flex-direction: column;
    width: 75%;
    height: 85vh;
    margin-left: 12.5%;
    background: var(--color-background__day);
    box-shadow: var(--box-shadow);
    border-radius: 15px;
    overflow: hidden;
}

.h_container {
    display: flex;
    width: 100%;
    height: 8vh;
    color: black;
    padding: 0% 5%;
    border-bottom: 2px solid var(--color-text__day);
}

.h_container h1 {
    margin: auto;
    margin-left: 0;
}

.formContainer {
    display: flex;
    height: 100%;
    /* background: red; */
    padding: 0 5%;
}

.form {
    display: flex;
    flex-direction: column;
    width: 50%;
    /* height: 100%; */
    margin-right: auto;
    margin-top: 10%;
    margin-bottom: auto;
    border: 2px solid #343434;
    border-radius: 10px;
    padding: 10px 10px;
    color: #343434;
    transition: .3s;
}

.formRed {
    border-color: red;
}

.inputDiv {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.inputDiv input {
    margin: auto auto auto 0;
    width: 100%;
    height: 40px;
    padding: 0px 5px;
    font-size: 1rem;
    color: white;
    border: none;
    border-bottom: 2px solid #969696;
    background: transparent;
    transition: .2s;
}

.inputDiv input::placeholder {
    color: silver;
}

.inputDiv input:hover {
    /*background: white;*/
    border-bottom: 2px solid white;
}

.inputDiv input:focus {
    border: none;
    outline: none;
    border-bottom: 2px solid white;
}

.checkboxDiv {
    display: flex;
    justify-content: flex-start;
    width: 30%;
    height: 30px;
    cursor: pointer;
}

.checkboxDiv input {
    margin-bottom: auto;
    margin-top: auto;
}

.checkboxDiv p {
    margin-left: 5px;
    margin-top: auto;
    margin-bottom: auto;
}

.btn {
    cursor: pointer;
    width: 100px;
    height: 40px;
    color: #343434;
    font-size: 1rem;
    border: 2px solid #343434;
    background-color: transparent;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    transition: .2s;
    font-weight: bold;
}

.btn:hover {
    background-color: #343434;
    color: #fff;
}

.welcomeContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    width: 43%;
    /* background-color: #343434; */
    height: 31vh;
    margin-top: 10%;
    margin-bottom: auto;
    margin-left: 0;
}

.welcomeContainer h2 {
    margin-bottom: 15px;
    color: #343434;
}

.welcomeContainer p {
    /* margin-top: 10px; */
    margin-bottom: 3px;
    color: silver;
}

.formError {
    color: red;
    font-weight: bold;
}